import { ipcMain } from 'electron';
import ps from 'ps-node';
export var setupProcessListeners = function () {
    ipcMain.on('request-running-apps', function (event) {
        ps.lookup({}, function (error, resultList) {
            if (error) {
                console.log('Error from setupProcessListeners function: ', error);
            }
            event.reply('response-running-apps', resultList);
        });
    });
};
